import { NoteData } from "chart-types";
import Vue from "vue";

export type NoteTypesDataType = {
  noteTypes: { text: string; value: number }[];
};

export type NoteTypesOption = {
  label: string;
  type: string;
  desc: string;
}

export type NoteTypesMethodsType = {
  noteOptions(note: NoteData): NoteTypesOption[];
};

export default Vue.extend({
  data(): NoteTypesDataType {
    return {
      noteTypes: [
        {
          text: "",
          value: 1
        },
        {
          text: "",
          value: 2
        },
        {
          text: "",
          value: 3
        },
        {
          text: "",
          value: 4
        },
        {
          text: "",
          value: 5
        },
        {
          text: "",
          value: 90
        },
        {
          text: "",
          value: 91
        },
        {
          text: "",
          value: 92
        },
        {
          text: "",
          value: 93
        },
        {
          text: "",
          value: 94
        },
        {
          text: "",
          value: 95
        },
        {
          text: "LED",
          value: 96
        },
        {
          text: "",
          value: 97
        },
        {
          text: "BPM",
          value: 98
        },
        {
          text: "EOF",
          value: 99
        },
        {
          text: "",
          value: 100
        }
      ]
    };
  },
  methods: {
    noteOptions(note: NoteData): NoteTypesOption[] {
      if ([3, 4].includes(note.type))
        return [
          {
            label: "width",
            type: "number",
            desc: "String(1)"
          },
          {
            label: "offsetNumer",
            type: "number",
            desc: "String"
          },
          {
            label: "offsetDenom",
            type: "number",
            desc: "String"
          }
        ];
      else if (note.type === 90) {
        // type
        const additionalOptions = this.noteOptions({
          ...note,
          type: Number(note.option[0]),
        })
        return [
          {
            label: "type",
            type: "number",
            desc: "Integer(type: 1, 2, 3, 4, 5)"
          },
          ...additionalOptions
        ];
      }
      else if (note.type === 91)
        return [
          {
            label: "objectType",
            type: "string",
            desc: "String"
          },
          {
            label: "objectName",
            type: "string",
            desc: "String"
          },
          {
            label: "option",
            type: "string",
            desc: "String"
          }
        ];
      else if (note.type === 93)
        return [
          {
            label: "beat",
            type: "number",
            desc: "Float"
          }
        ];
      else if (note.type === 94)
        return [
          {
            label: "source",
            type: "text",
            desc: "String(httphttps)"
          },
          {
            label: "width",
            type: "number",
            desc: "String(1)"
          },
          {
            label: "height",
            type: "number",
            desc: "String(1)"
          },
          {
            label: "offsetNumer",
            type: "number",
            desc: "String"
          },
          {
            label: "offsetDenom",
            type: "number",
            desc: "String"
          }
        ];
      else if (note.type === 95)
        return [
          {
            label: "length",
            type: "number",
            desc: "String(1)"
          }
        ];
      else if (note.type === 96)
        return [
          {
            label: "red",
            type: "number",
            desc: "String0-255"
          },
          {
            label: "green",
            type: "number",
            desc: "String0-255"
          },
          {
            label: "blue",
            type: "number",
            desc: "String0-255"
          }
        ];
      else if (note.type === 97)
        return [
          {
            label: "beat",
            type: "number",
            desc: "String[n/4]"
          }
        ];
      else if (note.type === 98)
        return [
          {
            label: "bpm",
            type: "number",
            desc: "StringBPM"
          }
        ];
      else if (note.type === 100)
        return [
          {
            label: "comment",
            type: "text",
            desc: "String"
          }
        ];
      else return [];
    }
  }
});
